@import url(https://fonts.googleapis.com/css?family=Roboto+Mono|Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #0A192F;
  padding: 50px;
  min-height: calc(100vh - 100px);
  color: #64ffda;
  font-family: 'Roboto', sans-serif; }
  .App .picture {
    display: flex;
    justify-content: center; }
    .App .picture .picture-wrapper {
      border: 2px solid #64ffda;
      width: 150px;
      height: 150px;
      border-radius: 50%; }
      .App .picture .picture-wrapper img {
        height: 150px;
        width: 150px;
        -webkit-filter: sepia(100%) saturate(300%) brightness(70%) hue-rotate(180deg);
                filter: sepia(100%) saturate(300%) brightness(70%) hue-rotate(180deg);
        border-radius: 50%; }
        .App .picture .picture-wrapper img:hover {
          -webkit-filter: none;
                  filter: none; }
  .App .intro {
    text-align: left;
    padding-top: 80px;
    padding-left: 5vw; }
    .App .intro .greet {
      font-family: 'Roboto Mono', monospace; }
    .App .intro .name {
      color: #E6F1FF;
      font-family: 'Roboto', sans-serif;
      font-weight: 700;
      font-size: calc(8px + 8vmin); }
    .App .intro .what-i-do {
      font-family: 'Roboto', sans-serif;
      font-weight: 700;
      font-size: calc(8px + 8vmin);
      color: #8892B0; }
    .App .intro .description {
      color: #8892B0;
      padding-top: 30px;
      max-width: 500px;
      letter-spacing: 1px;
      line-height: 24px; }

